.military-ranks-body {
  margin: 10vh 0 0 0;
  overflow-y: hidden;

  .come-back-btn-container {
    @include flex(row, "", top, center);
    position: fixed;
    width: 9vw;
    height: 3vh;
    top: 70px;
    left: 0;
    z-index: 25;
    font-size: 2rem;
    text-shadow: 0 0 5px $shadow;
    color: $grey2;
    padding: 1vh 2vw;
    border-radius: 5px;
    transition: 0.3s;

    &:hover {
      cursor: pointer;
      transform: translateX(-1vw);
      color: $yellow3;
      text-shadow: 0 0 10px $yellow1;
    }

    h4 {
      text-align: left;
      margin: 0 0.5vw;
      font-weight: 400;
      font-size: 1.2rem;
      letter-spacing: 2px;
    }
  }

  .military-ranks-container {
    max-width: 80vw;
    margin: 0 auto;

    .title-row-container {
      position: relative;
      margin: 2vh auto;
      padding: 10px 0 10px 0;
      border-radius: 10px;
      text-align: center;
      background: linear-gradient(
        135deg,
        $white2 0%,
        $grey4 25%,
        $black4 50%,
        $grey4 75%,
        $white2 100%
      );

      &:hover {
        h3 {
          text-shadow: 0 0 10px $green1;
        }
      }

      h3 {
        border: 1px solid $black3;
        border-radius: 7px;
        background: $black2;
        letter-spacing: 5px;
        text-transform: uppercase;
        padding: 10px;
        margin: 0 10px;
        box-shadow: inset 0 0 10px $shadow;
        transition: 0.2s linear;
      }

      p {
        border: 1px solid $black3;
        border-radius: 7px;
        background: linear-gradient(
          90deg,
          $black1 0%,
          $black2 25%,
          $black3 35%,
          $black4 50%,
          $black3 65%,
          $black2 75%,
          $black1 100%
        );
        padding: 10px;
        margin: 10px 10px 0 10px;
        box-shadow: inset 0 0 10px $shadow;
        text-shadow: 0 1px 1px $shadow;
        transition: 0.2s;
        letter-spacing: 4px;
        font-size: 1.3rem;
        line-height: 30px;
        font-family: $agFB;
      }
    }

    .rank-row-container {
      position: relative;
      margin: 2vh auto;
      padding: 10px 0 0;
      border-radius: 10px;
      text-align: center;
      background: linear-gradient(
        135deg,
        $white2 0%,
        $grey4 25%,
        $black4 50%,
        $grey4 75%,
        $white2 100%
      );

      &:hover {
        cursor: pointer;

        h3 {
          text-shadow: 0 0 10px $green1;
        }

        .left-top {
          position: absolute;
          top: -1px;
          left: -1px;
          height: 45%;
          width: 49%;
          border: 1px solid $green1;
          box-shadow: 0 0 10px $green1;
          border-radius: 11px;
          z-index: -1;
        }

        .top-right {
          position: absolute;
          top: -1px;
          right: -1px;
          height: 45%;
          width: 49%;
          border: 1px solid $green1;
          box-shadow: 0 0 10px $green1;
          border-radius: 11px;
          z-index: -1;
        }
        .right-bottom {
          position: absolute;
          right: -1px;
          bottom: -1px;
          height: 45%;
          width: 49%;
          border: 1px solid $green1;
          box-shadow: 0 0 10px $green1;
          border-radius: 11px;
          z-index: -1;
        }

        .bottom-left {
          position: absolute;
          bottom: -1px;
          left: -1px;
          height: 45%;
          width: 49%;
          border: 1px solid $green1;
          box-shadow: 0 0 10px $green1;
          border-radius: 11px;
          z-index: -1;
        }
      }

      .reduced {
        height: 0;
        padding-bottom: 10px;
      }

      .depoyed {
        height: 700px;
      }

      .hidden {
        display: none;
      }

      .visible {
        display: block;
      }

      .left-top {
        position: absolute;
        top: -1px;
        left: -1px;
        height: 0%;
        width: 0%;
        border: 1px solid $translucent;
        border-radius: 11px;
        z-index: -1;
        transition: 0.2s;
      }
      .top-right {
        position: absolute;
        top: -1px;
        right: -1px;
        height: 0%;
        width: 0%;
        border: 1px solid $translucent;
        border-radius: 11px;
        z-index: -1;
        transition: 0.2s;
      }
      .right-bottom {
        position: absolute;
        bottom: -1px;
        right: -1px;
        height: 0%;
        width: 0%;
        border: 1px solid $translucent;
        border-radius: 11px;
        z-index: -1;
        transition: 0.2s;
      }
      .bottom-left {
        position: absolute;
        bottom: -1px;
        left: -1px;
        height: 0%;
        width: 0%;
        border: 1px solid $translucent;
        border-radius: 11px;
        z-index: -1;
        transition: 0.2s;
      }

      h3 {
        border: 1px solid $black3;
        border-radius: 7px;
        background: $black2;
        letter-spacing: 5px;
        text-transform: uppercase;
        padding: 10px;
        margin: 0 10px;
        box-shadow: inset 0 0 10px $shadow;
        transition: 0.2s linear;
      }

      .general-officer {
        @include grid(20% 20% 20% 20% 20%, "", "", top);
        text-align: center;
      }
      .senior-officer {
        @include grid(33.33% 33.33% 33.33%, "", "", top);
        text-align: center;
      }
      .junior-officer {
        @include grid(25% 25% 25% 25%, "", "", top);
        text-align: center;
      }
      .non-commissioned-officer {
        @include grid(20% 20% 20% 20% 20%, "", "", top);
        text-align: center;
      }
      .non-commissioned-member {
        @include grid(20% 20% 20% 20% 20%, "", "", top);
        text-align: center;
      }

      .rank-container {
        border: 1px solid $black3;
        border-radius: 7px;
        background: linear-gradient(
          90deg,
          $black1 0%,
          $black2 25%,
          $black3 35%,
          $black4 50%,
          $black3 65%,
          $black2 75%,
          $black1 100%
        );
        padding: 10px;
        margin: 10px;
        box-shadow: inset 0 0 10px $shadow;
        transition: 0.2s;

        &:hover {
          h4 {
            text-shadow: 0 0 10px $green1;
          }
        }

        h4 {
          text-transform: uppercase;
          letter-spacing: 3px;
          font-size: 1rem;
        }

        .rank-img-container {
          max-width: 250px;
          margin: 0 auto;

          img {
            width: 100%;
          }
        }

        p {
          margin: 0 auto;
          text-align: center;
          max-width: 100%;
        }
      }
    }
  }
}
