.military-outfits-cards-description {
  position: fixed;
  height: auto;
  max-height: 90vh;
  width: fit-content;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -20%);
  border: 1px solid $black3;
  box-shadow: 0 0 10px $shadow;
  border-radius: 15px;
  background: linear-gradient(
    135deg,
    $black1 0%,
    $black2 25%,
    $black3 35%,
    $black4 50%,
    $black3 65%,
    $black2 75%,
    $black1 100%
  );
  padding: 10px;
  z-index: 10;

  .main-container {
    @include grid(25vw 25vw, "", center, top);

    .img-container {
      width: 100%;
      @include flex(row, "", center, center);

      img {
        border-radius: 10px;
        width: 98%;
        margin-right: 10px;
        border: 1px solid $black4;
      }
    }

    .outfit-description-container {
      width: 100%;
      height: 100%;
      @include flex(row, "", center, center);

      .outfit-border-container {
        height: 100%;
        margin-left: 10px;
        background: $black2;
        border: 1px solid $black4;
        border-radius: 10px;
        box-shadow: inset 0 0 10px $shadow;
      }
    }
  }

  .outfit-name {
    margin: 2vh auto 0;
    max-width: 15vw;
    text-transform: uppercase;
    letter-spacing: 10px;
  }

  .outfit-armor-components {
    text-transform: uppercase;
    letter-spacing: 10px;
    margin: 4vh 10px 5vh;
    font-size: 1.2rem;
    text-align: center;

    h4 {
      letter-spacing: 10px;
      margin: 10px;
    }
  }
  .outfit-weapon-components {
    text-transform: uppercase;
    letter-spacing: 10px;
    margin: 4vh 10px 5vh;
    font-size: 1.2rem;
    text-align: center;

    h4 {
      letter-spacing: 10px;
      margin: 10px;
    }
  }

  .outfit-description {
    margin: 4vh 10px 5vh;
    line-height: 25px;
    font-size: 1.2rem;
    letter-spacing: 2px;
    text-align: center;

    h3 {
      text-transform: uppercase;
      letter-spacing: 10px;
    }
  }

  .close-explanations {
    transform: translateY(5vh);
    margin: 50px;
    font-size: 1.5rem;

    &:hover {
      cursor: pointer;
      text-shadow: 0 0 10px $blue2;
    }
  }
}

.visible {
  display: block;
}
.hidden {
  display: none;
}
