.military-menu-body {
  margin: 10vh 0 0 0;
  overflow-y: hidden;

  .come-back-btn-container {
    @include flex(row, "", top, center);
    position: fixed;
    width: 9vw;
    height: 3vh;
    top: 70px;
    left: 0;
    z-index: 25;
    font-size: 2rem;
    text-shadow: 0 0 5px $shadow;
    color: $grey2;
    padding: 1vh 2vw;
    border-radius: 5px;
    transition: 0.3s;

    &:hover {
      cursor: pointer;
      transform: translateX(-1vw);
      color: $yellow3;
      text-shadow: 0 0 10px $yellow1;
    }

    h4 {
      text-align: left;
      margin: 0 0.5vw;
      font-weight: 400;
      font-size: 1.2rem;
      letter-spacing: 2px;
    }
  }

  .military-menu-container {
    @include flex(row, "", space-around, center);
    width: 95%;
    margin: 0 auto;

    h3 {
      text-transform: uppercase;
      letter-spacing: 5px;
    }
    .container {
      margin: 10px;
    }

    .metiers {
      width: 500px;
      @include flex(column, "", center, center);

      img {
        border-radius: 10px;
        width: 100%;
        max-height: 858px;
      }
    }
    .grades {
      width: 500px;
      @include flex(column, "", center, center);

      img {
        border-radius: 10px;
        width: 100%;
        max-height: 858px;
      }
    }
    .brevets {
      width: 500px;
      @include flex(column, "", center, center);

      img {
        border-radius: 10px;
        width: 100%;
        max-height: 858px;
      }
    }
    .tenues {
      width: 500px;
      @include flex(column, "", center, center);

      img {
        border-radius: 10px;
        width: 100%;
        max-height: 858px;
      }
    }
  }
}
