.administration-body {
  background: $black3;

  .social-network-title-container {
    @include flex(column, "", center, center);
    margin: 70px 0 0 0;
    height: 70px;
    background: radial-gradient(circle, #330000 3%, #000000 100%);
    box-shadow: 0 0 10px $black1;

    h2 {
      font-size: 2rem;
      padding: 5px;
      background: -webkit-linear-gradient(95deg, $yellow1, $white1, $yellow1);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      -webkit-filter: drop-shadow(0 0 5px $yellow1);
      letter-spacing: 10px;
      animation: titleWaveYellow 3s infinite;
      text-transform: uppercase;
    }
  }

  .users-cards-container {
    @include grid(25% 25% 25% 25%, auto, center, center);
    max-width: 75vw;
    margin: 0 auto;
  }
}
