.military {
  z-index: 15;
  position: fixed;
  top: 0;
  width: 100%;
  height: 70px;
  @include grid(10% 10% 10% 40% 10% 10% 10%, "", center, center);
}

.civilian {
  z-index: 15;
  position: fixed;
  top: 0;
  width: 100%;
  height: 70px;
  @include grid(10% 10% 10% 40% 10% 10% 10%, "", center, center);
}

.header-container {
  background: linear-gradient($black3, $black1);
  box-shadow: 0px 0px 6px $shadow;

  a {
    text-decoration: none;
    text-align: center;
    border: none;
    width: 120px;
    background: $white1;
    transition: 0.2s;
    text-transform: uppercase;
    letter-spacing: 4px;
    font-size: 1.1rem;
    font-weight: 400;
    font-family: $agFBB;
    cursor: pointer;
    color: $black2;
  }

  .civilian-btn {
    @include flex(row, "", space-around, center);
    width: fit-content;
    padding: 15px 10px;
    background: linear-gradient($black3, $black1);
    border-radius: 5px;
    text-align: center;
    transition: 0.2s;
    box-shadow: 0px 0px 10px $shadow;
    color: $white1;
    text-transform: uppercase;
    letter-spacing: 4px;
    font-size: 1.1rem;
    font-weight: 400;
    font-family: $agFBB;
    cursor: pointer;

    &:hover {
      box-shadow: 0 0 10px 2px $yellow1;
      background: linear-gradient($black1, $black3);
      color: $yellow1;
    }

    &:visited {
      color: none;
    }
  }

  .military-btn {
    @include flex(row, "", space-around, center);
    width: fit-content;

    padding: 10px 15px;
    background: linear-gradient($black3, $black1);
    border-radius: 5px;
    text-align: center;
    transition: 0.2s;
    box-shadow: 0px 0px 10px $shadow;
    color: $grey1;
    text-transform: uppercase;
    letter-spacing: 4px;
    font-size: 1.1rem;
    font-weight: 400;
    font-family: $agFBB;
    cursor: pointer;

    &:hover {
      box-shadow: 0 0 5px $blue2;
      background: linear-gradient($black1, $black3);
      color: $blue2;
    }

    &:visited {
      color: none;
    }
  }

  .user-active-green-dot {
    margin: 0 0 3px 10px;
    border-radius: 50%;
    height: 7px;
    width: 7px;
    background: $green3;
    border: 1px solid $green2;
    box-shadow: 0 0 10px 2px $green3;
  }

  .user-inactive-red-dot {
    margin: 0 0 3px 10px;
    border-radius: 50%;
    height: 7px;
    width: 7px;
    background: $red4;
    border: 1px solid $red2;
    box-shadow: 0 0 10px 2px $red4;
  }

  .civilian-img-container {
    @include flex(row, "", center, center);
    margin: 0 auto;
    height: 60px;
    width: 30%;
    background: linear-gradient($black3, $black1);
    border-radius: 5px;
    box-shadow: 0px 0px 5px $shadow;
    transition: 0.2s;

    &:hover {
      box-shadow: 0px 0px 5px $yellow1;
      background: linear-gradient($black1, $black3);

      img {
        transform: scale(0.8);
      }
    }

    img {
      margin: 0 auto;
      height: 80%;
      transition: 0.2s;
    }
  }

  .military-img-container {
    @include flex(row, "", center, center);
    margin: 0 auto;
    height: 60px;
    width: 30%;
    background: linear-gradient($black3, $black1);
    border-radius: 5px;
    box-shadow: 0px 0px 5px $shadow;
    transition: 0.2s;

    &:hover {
      box-shadow: 0px 0px 5px $blue2;
      background: linear-gradient($black1, $black3);

      img {
        transform: scale(0.8);
      }
    }
    img {
      margin: 0 auto;
      height: 80%;
      transition: 0.2s;
    }
  }
}
