.comment-card-container {
  background: $black4;
  padding: 5px;
  margin: 10px 0;
  border-radius: 5px;

  .comment-card-title-container {
    position: relative;
    @include flex(row, "", space-between, center);

    .comment-card-title {
      color: $yellow1;
      margin: 5px 5px;
      letter-spacing: 2px;
    }

    .user-container {
      @include flex(row, "", space-between, center);

      h4 {
        width: 150px;
        margin: 10px 10px 10px 0;
        letter-spacing: 2px;
        font-size: 1.3rem;
        color: $yellow1;
      }

      .role-site-container {
        @include flex("row", "wrap", center, center);
        width: fit-content;
        margin: 2px 2px 0;

        .role-circle {
          height: 10px;
          width: 10px;
          border-radius: 50%;
        }

        .administrateur {
          background: $red5;
          border: 1px solid $red3;
          box-shadow: 0 0 10px 3px $red4;
        }

        .membre {
          background: $blue4;
          border: 1px solid $blue3;
          box-shadow: 0 0 10px 3px $blueExplo;
        }

        .invité {
          background: $grey1;
          border: 1px solid $grey4;
          box-shadow: 0 0 10px 3px $white1;
        }

        .user-role {
          margin: 5px 10px;
          letter-spacing: 2px;
          font-size: 1.1rem;
          text-transform: capitalize;
        }
      }

      .role-corpo-container {
        @include flex("row", "wrap", center, center);
        width: fit-content;
        margin: 2px 2px 0;

        .role-circle {
          height: 10px;
          width: 10px;
          border-radius: 50%;
        }

        .primarque {
          background: $yellow2;
          border: 1px solid $yellow1;
          box-shadow: 0 0 10px 3px $yellow2;
        }

        .executeur {
          background: $orange2;
          border: 1px solid $orange1;
          box-shadow: 0 0 10px 3px $orange2;
        }

        .membre {
          background: $blue4;
          border: 1px solid $blue3;
          box-shadow: 0 0 10px 3px $blueExplo;
        }

        .invité {
          background: $grey1;
          border: 1px solid $grey4;
          box-shadow: 0 0 10px 3px $white1;
        }

        .user-role {
          margin: 5px 10px;
          letter-spacing: 2px;
          font-size: 1.1rem;
          text-transform: capitalize;
        }
      }
    }

    .comment-actions-container {
      width: 5%;
      @include grid(50% 50%, "", center, center);

      span {
        position: relative;
        background: none;
        border: none;
        color: $white1;

        &:hover {
          cursor: pointer;
          color: $yellow1;
          border: none;
          box-shadow: none;
        }
      }
    }

    .publication-time-container {
      position: relative;
      padding: 0 0 5px 0;

      p {
        margin: 5px 5px;
        padding: 0 5px;
        letter-spacing: 3px;
      }

      &:after {
        content: "";
        position: absolute;
        height: 1px;
        bottom: 0;
        left: 0;
        width: 100%;
        background: linear-gradient(
          135deg,
          $yellow1 0%,
          $yellow2 5%,
          $yellow3 50%,
          $yellow2 95%,
          $yellow1 100%
        );
      }
    }
  }

  .comment-content {
    position: relative;
    font-size: 1.3rem;
    letter-spacing: 1px;
    @include flex(column, "", flex-start, center);

    p {
      width: 100%;
    }

    textarea {
      margin: 10px 0 35px 0;
      border-radius: 0 0 5px 5px;
      resize: none;
      height: 5vh;
      overflow: auto;
      width: 99%;
      letter-spacing: 1px;
      font-size: 1.3rem;
      font-weight: $agFBB;
      outline: none;
      border-bottom: 2px solid $red3;
    }

    span {
      position: absolute;
      bottom: 0;
      right: 0;
      border: 1px solid $black1;
      background: $white1;
      color: $black2;
      padding: 5px 10px;
      border-radius: 0 5px 5px 5px;
      text-transform: uppercase;
      font-size: 1rem;
      letter-spacing: 2px;
      transition: 0.2s;

      &:hover {
        border: 1px solid $yellow1;
        background: linear-gradient(
          135deg,
          $black1 0%,
          $black2 5%,
          $black3 50%,
          $black2 95%,
          $black1 100%
        );
        color: $white1;
        box-shadow: inset 0 0 5px $grey4;
      }
      &:focus {
        border: 1px solid $red2;
        background: linear-gradient(
          135deg,
          $black1 0%,
          $black2 5%,
          $black3 50%,
          $black2 95%,
          $black1 100%
        );
        color: $white1;
      }
    }
  }
}
