.user-card {
  margin: 20px 0 20px 0;
  border: 1px solid $black4;
  border-radius: 10px;
  box-shadow: 0 2px 5px 2px $shadow;

  .card-infos-container {
    margin: 0 10px 10px 10px;

    .input-wrapper {
      width: 100%;
      position: relative;

      input {
        width: 100%;
        margin: 10px 0;
        height: 30px;
        font-size: 1.5rem;
        font-family: $agFB;
        box-sizing: border-box;
      }

      .name-validator {
        position: absolute;
        height: 10px;
        width: 10px;
        top: 20px;
        right: 20px;
        background: $green1;
        border-radius: 50%;

        &:hover {
          cursor: pointer;
        }
      }
    }

    .user-card-name {
      margin: 10px 0;
    }

    .profile-img-container {
    }

    .role-site-container {
      @include flex("row", "wrap", flex-start, center);
      width: 100%;
      margin: 5px 2px 0;

      .role-circle {
        height: 10px;
        width: 10px;
        border-radius: 50%;
      }

      .administrateur {
        background: $red5;
        border: 1px solid $red3;
        box-shadow: 0 0 10px 3px $red4;
      }

      .membre {
        background: $blue4;
        border: 1px solid $blue3;
        box-shadow: 0 0 10px 3px $blueExplo;
      }

      .invité {
        background: $grey1;
        border: 1px solid $grey4;
        box-shadow: 0 0 10px 3px $white1;
      }

      .user-role {
        margin: 5px 10px;
        letter-spacing: 2px;
        font-size: 1.1rem;
        text-transform: capitalize;
      }
    }

    .role-corpo-container {
      @include flex("row", "wrap", flex-start, center);
      width: 100%;
      margin: 0 2px 10px;

      .role-circle {
        height: 10px;
        width: 10px;
        border-radius: 50%;
      }

      .primarque {
        background: $yellow2;
        border: 1px solid $yellow1;
        box-shadow: 0 0 10px 3px $yellow2;
      }

      .executeur {
        background: $orange2;
        border: 1px solid $orange1;
        box-shadow: 0 0 10px 3px $orange2;
      }

      .membre {
        background: $blue4;
        border: 1px solid $blue3;
        box-shadow: 0 0 10px 3px $blueExplo;
      }

      .invité {
        background: $grey1;
        border: 1px solid $grey4;
        box-shadow: 0 0 10px 3px $white1;
      }

      .user-role {
        margin: 5px 10px;
        letter-spacing: 2px;
        font-size: 1.1rem;
        text-transform: capitalize;
      }
    }

    .role-title {
      width: 30%;
      letter-spacing: 2px;
      margin: 0 10px 0 0;
    }

    .actions-container {
      width: 100%;
      @include flex(row, "wrap", space-between, center);

      .edit-btn {
        // margin: 5px 5px 0 0;
        padding: 5px;
        border-top: 1px solid $grey4;
        border-right: 1px solid $black1;
        border-left: 1px solid $black1;
        border-bottom: 1px solid $black1;
        border-radius: 5px;
        width: fit-content;
        text-transform: uppercase;
        text-align: left;
      }
    }
  }
}
