// MIXINS =>

// Flex ->
@mixin flex($fd, $fw, $jc, $ai) {
  display: flex;
  flex-direction: $fd;
  flex-wrap: $fw;
  justify-content: $jc;
  align-items: $ai;
}

// Grid ->
@mixin grid($gtc, $gtr, $jc, $ai) {
  display: grid;
  grid-template-columns: $gtc;
  grid-template-raws: $gtr;
  justify-items: $jc;
  align-items: $ai;
}

// Text gradient ->
@mixin fireG() {
  background: -webkit-linear-gradient(180deg, $yellow2, $white1, $yellow2);
  background-clip: text;
  filter: drop-shadow(0 0 5px $shadow);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-filter: drop-shadow(0 0 5px $shadow);
}

// GENERAL =>
* {
  user-select: none;
  scroll-behavior: smooth;
  font-family: $agFB;
}

body {
  margin: 0;
  padding: 0;
  display: block;
  background: $black3;
  color: $white1;
}

a {
  text-decoration: none;

  &:visited {
    color: none;
  }
}

// Department mixins

@mixin departmentLogos($color) {
  // height: auto;
  border-radius: 50%;
  border: 2px solid $color;
  // padding: 1px;
  transition: 0.2s;
}

@mixin link($color1, $color2) {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  background: $color1;
  box-shadow: 0 0 10px $color2;
}

@mixin job-title($color1, $color2) {
  position: absolute;
  @include flex("", "", center, center);
  width: 142%;
  top: 24vh;
  left: 50%;
  transform: translatex(-50%);
  border: 2px solid $color1;
  border-radius: 10px;
  box-shadow: 0 0 10px $color2;
  background: $black3;

  h3 {
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 3px;
  }
}

@mixin animation($name, $time) {
  animation: $name $time infinite;
}

// scroll bar Y axis ->
@mixin scrollY($color1) {
  &::-webkit-scrollbar {
    background: none;
    width: 0.2vw;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: $color1;
  }
}

