//  Reveal
@keyframes opacity {
  0% {
    visibility: hidden;
  }
  25% {
    visibility: hidden;
    opacity: 0;
  }
  50% {
    visibility: visible;
    opacity: 0;
  }
  75% {
    visibility: visible;
    opacity: 0.5;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}

@keyframes opacityReverse {
  0% {
    display: content;
    opacity: 1;
  }
  25% {
    display: content;
    opacity: 0.5;
  }
  50% {
    display: content;
    opacity: 0;
  }
  75% {
    display: none;
    opacity: 0;
  }
  100% {
    display: none;
    opacity: 0;
  }
}

