.form-container {
  position: absolute;
  top: 80px;
  @include flex(column, "", center, center);
  width: 40%;
  min-width: 400px;
  max-width: 1000px;
  margin: 50px auto 0;
  box-shadow: 0 0 5px 2px $shadow;
  border-radius: 5px;
  border: 1px solid $black4;
  background: linear-gradient(
    135deg,
    $black1 0%,
    $black2 25%,
    $black3 35%,
    $black4 50%,
    $black3 65%,
    $black2 75%,
    $black1 100%
  );

  .form-title-container {
    font-size: 1.1rem;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-align: justify;
    max-width: 90%;
  }

  .form-content {
    width: 100%;
    @include flex(column, "", center, center);

    .individual-infos {
      padding: 0;
      width: 384px;
      margin: 0 auto;

      li {
        @include flex(column, "", "", flex-start);
        text-align: left;
        width: 100%;

        label {
          font-size: 1.2rem;
          letter-spacing: 1px;
        }

        input,
        textarea {
          border-radius: 3px;
          margin: 5px 0 10px;
          font-size: 1.1rem;
          box-sizing: border-box;
          width: 100%;
          border: 0;
          padding: 5px;
          outline: none;
          letter-spacing: 1px;
          transition: 0.2s;
          border: 1px solid $translucent;
          box-shadow: inset 0 0 5px $shadow;

          &:focus {
            background: $black4;
            color: $yellow1;
            border: 1px solid $yellow1;
            font-weight: 600;
            letter-spacing: 2px;
          }
        }

        textarea {
          resize: vertical;
          min-height: 100px;
          height: fit-content;
        }
      }
      .submit-btn {
        border: 1px solid $translucent;
        border-radius: 3px;
        background: $yellow1;
        color: $black3;
        cursor: pointer;
        width: 80px;
        font-size: 1rem;
        text-transform: uppercase;
        font-weight: bold;
        letter-spacing: 2px;
        margin: 0 0 20px;
        padding: 2px 0 0;
        transition: 0.2s;

        &:hover {
          border: 1px solid $yellow1;
          background: $black4;
          color: $yellow1;
          box-shadow: 0 0 5px 1px $yellow1;
        }
      }
    }
  }

  ul {
    list-style-type: none;
  }
}

.social-network-container {
  @include flex(column, "", center, center);
  position: absolute;
  top: 600px;
  width: 40%;
  min-width: 400px;
  max-width: 1000px;
  margin: 50px auto 0;
  box-shadow: 0 0 5px 2px $shadow;
  border-radius: 5px;
  border: 1px solid $black4;
  background: linear-gradient(
    135deg,
    $black1 0%,
    $black2 25%,
    $black3 35%,
    $black4 50%,
    $black3 65%,
    $black2 75%,
    $black1 100%
  );

  .social-network-container-title {
    position: relative;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: $white1;
    font-size: 1.2rem;
    padding: 15px 0;
    margin: 0 auto 10px;
    text-align: center;

    &:after {
      position: absolute;
      content: "";
      height: 1px;
      width: 99%;
      background: $yellow1;
      bottom: 0;
      left: 0;
    }
  }

  ul {
    width: 100%;
    @include flex(row, "", center, center);
    padding: 0;
    margin: 15px auto;

    li {
      @include flex(row, "", center, center);
      list-style-type: none;
      width: 25%;

      a {
        @include flex(row, "", center, center);
        text-decoration: none;
        color: $yellow1;

        &:hover {
          cursor: pointer;
        }

        img {
          transition: 0.2s;
          height: 25px;
          width: 25px;
          filter: drop-shadow(0 0 5px $shadow);

          &:hover {
            filter: drop-shadow(0 0 10px $white1);
          }
        }
      }
    }
  }
}
