// CETTE PAGE DE STYLE GERE LES ELEMENTS IMBRIQUES SUIVANTS:
// pages/militaryCareers.js
// Components/militaryDepartments.js
// Components/militaryCareerCards.js

// militaryCareers.js ----------------------------------------------------------------------------------------
.military-careers {
  max-width: 980vw;
  position: relative;
  overflow-x: auto;
  overflow-y: hidden;
  background: url(../../../assets/img/civilian-careers-background.jpg) fixed
    center/cover;
}

.slide-btn-container {
  position: fixed;
  top: 48vh;
  width: 100%;
  z-index: 15;

  .left-btn-container {
    filter: drop-shadow(0 0 5px $shadow);

    &:hover {
      filter: drop-shadow(0 0 5px $yellow1);
    }
  }
  .right-btn-container {
    filter: drop-shadow(0 0 5px $shadow);

    &:hover {
      filter: drop-shadow(0 0 5px $yellow1);
    }
  }

  button {
    position: fixed;
    background: none;
    border: none;
    height: 3.5vh;
    width: 1.5vw;
    font-size: 3rem;
    transition: 0.1s;
    background: $white1;

    &:hover {
      cursor: pointer;
      color: $yellow1;
    }
  }
  .left-btn {
    clip-path: polygon(100% 0%, 75% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);

    &:hover {
      transform: translateX(-0.2vw) scale(1.2);
    }
  }
  .right-btn {
    clip-path: polygon(75% 0%, 100% 50%, 75% 100%, 0% 100%, 25% 50%, 0% 0%);

    &:hover {
      transform: translateX(0.2vw) scale(1.2);
    }
  }
}

.left-btn {
  left: 1vw;
}

.right-btn {
  right: 1vw;
}

.military-career-container {
  // DEPARTMENTS CONTAINERS //

  .departments-container {
    @include grid(
      100vw 100vw 100vw 100vw 100vw 100vw 100vw 100vw,
      "",
      center,
      center
    );

    // militaryDepartments.js ------------------------------------------------------------------------------
    #container-staff {
      @include flex("row", "", center, center);
      background: url(../../../assets/img/staff-background.jpg) fixed
        center/cover;
      height: 100vh;
      width: 100vw;
    }
    #container-aerospatialDepartment {
      @include flex("row", "", "", "");
      background: url(../../../assets/img/aerospatialDepartment-background.jpg)
        fixed center/cover;
      height: 100vh;
      width: 100%;
    }
    #container-infantryDepartment {
      @include flex("row", "", "", "");
      background: url(../../../assets/img/infantryDepartment-background.jpg)
        fixed center/cover;
      height: 100vh;
      width: 100%;
    }
    #container-logisticsDepartment {
      @include flex("row", "", "", "");
      background: url(../../../assets/img/logisticsDepartment-background.jpg)
        fixed center/cover;
      height: 100vh;
      width: 100%;
    }
    #container-militaryResearchDepartment {
      @include flex("row", "", "", "");
      background: url(../../../assets/img/militaryResearchDepartment-background.jpg)
        fixed center/cover;
      height: 100vh;
      width: 100%;
    }
    #container-militaryAcademy {
      @include flex("row", "", "", "");
      background: url(../../../assets/img/militaryAcademy-background.jpg) fixed
        center/cover;
      height: 100vh;
      width: 100%;
    }
    #container-militaryMedicineDepartment {
      @include flex("row", "", "", "");
      background: url(../../../assets/img/militaryMedicineDepartment-background.jpg)
        fixed center/cover;
      height: 100vh;
      width: 100%;
    }
    #container-blackOpsDivision {
      @include flex("row", "", "", "");
      background: url(../../../assets/img/blackOpsDivision-background.jpg) fixed
        center/cover;
      height: 100vh;
      width: 100%;
    }

    .military-departments {
      position: relative;
      font-size: 1rem;
      height: 100vh;
      width: 100vw;
      @include grid(auto, "", center, center);

      .img-container {
        position: relative;

        .logo-staff {
          @include departmentLogos($black1);

          &:hover {
            @include animation(waveYellow, 2s);
            cursor: pointer;
          }
        }
        .logo-aerospatialDepartment {
          @include departmentLogos($black1);

          &:hover {
            @include animation(waveYellow, 2s);
            cursor: pointer;
          }
        }
        .logo-infantryDepartment {
          @include departmentLogos($black1);

          &:hover {
            @include animation(waveBlueExplo, 2s);
            cursor: pointer;
          }
        }
        .logo-logisticsDepartment {
          @include departmentLogos($black1);

          &:hover {
            @include animation(waveYellow, 2s);
            cursor: pointer;
          }
        }
        .logo-militaryResearchDepartment {
          @include departmentLogos($black1);

          &:hover {
            @include animation(waveGreen, 2s);
            cursor: pointer;
          }
        }
        .logo-militaryAcademy {
          @include departmentLogos($black1);

          &:hover {
            @include animation(waveOrange, 2s);
            cursor: pointer;
          }
        }
        .logo-militaryMedicineDepartment {
          @include departmentLogos($black1);

          &:hover {
            @include animation(waveWhite, 2s);
            cursor: pointer;
          }
        }
        .logo-blackOpsDivision {
          @include departmentLogos($black1);

          &:hover {
            @include animation(waveRedMed, 2s);
            cursor: pointer;
          }
        }

        .not-active {
          width: 15vw;
          border-radius: 50%;
        }
        .active {
          width: 15vw;
          border-radius: 50%;
          transform: translateX(-25vw);
        }
      }

      .reduced {
        height: 4vh;
        transition: 0.5s;
        animation: waveRed 0.6s 0s forwards;
        border: 2px solid $translucent;
        @include flex(column, "", center, center);
        cursor: pointer;
      }

      .deployed {
        height: 45vh;
        transition: 0.7s;
        animation: waveBlue 1s 0s infinite;
      }

      .visible {
        animation: opacity 0.7s forwards linear;
      }

      .hidden {
        display: none;
      }

      .career-card-container {
        transform: translate(-25vw);
        width: 10vw;
        height: 10vw;
        background: $black2;
        border-radius: 20px;

        &::-webkit-scrollbar {
          background: none;
          height: 0.2vh;
        }

        &::-webkit-scrollbar-thumb {
          border-radius: 10px;
          background: $grey2;
        }
      }

      .job-description-container {
        @include flex("", "", center, center);
        position: absolute;
        left: 96%;
        top: 50%;
        width: 46vw;
        height: 50vh;
        background: $black2;
        border-radius: 10px;
        overflow-x: hidden;
        overflow-y: hidden;
        grid-row-gap: 5vh;

        .staff {
          @include grid(25% 25% 25% 25%, "", center, center);
          background: $black2;
          border-radius: 7px;
          width: 98.4%;
          height: 97%;
          border: 1px solid $black4;
          box-shadow: inset 0 0 10px $shadow;
        }

        .aerospatialDepartment {
          @include grid(25% 25% 25% 25%, "", center, center);
          background: url(../../../assets/img/primaris-academy-background.jpg)
            fixed center/cover;
          background: $black2;
          border-radius: 7px;
          width: 98.4%;
          height: 97%;
          border: 1px solid $black4;
        }

        .infantryDepartment {
          @include grid(25% 25% 25% 25%, auto, center, center);
          background: $black2;
          border-radius: 7px;
          width: 98.4%;
          height: 97%;
          border: 1px solid $black4;
        }
        .logisticsDepartment {
          @include grid(25% 25% 25% 25%, "", center, center);
          background: $black2;
          border-radius: 7px;
          width: 98.4%;
          height: 97%;
          border: 1px solid $black4;
        }

        .militaryResearchDepartment {
          width: 100%;
          height: 100%;
          @include grid(25% 25% 25% 25%, "", center, center);
          grid-row-gap: 1vh;
          grid-column-gap: 0.05vw;
          background: $black2;
          border-radius: 7px;
          width: 98.4%;
          height: 97%;
          border: 1px solid $black4;
          overflow-y: scroll;
          overflow-x: hidden;

          @include scrollY($green3);
        }
        .militaryAcademy {
          @include grid(25% 25% 25% 25%, "", center, center);
          grid-row-gap: 1vh;
          grid-column-gap: 0.05vw;
          background: $black2;
          border-radius: 7px;
          width: 98.4%;
          height: 97%;
          border: 1px solid $black4;
          overflow-y: scroll;
          overflow-x: hidden;

          @include scrollY($orange3);
        }
        .militaryMedicineDepartment {
          @include grid(25% 25% 25% 25%, "", center, center);
          grid-row-gap: 1vh;
          grid-column-gap: 0.05vw;
          background: $black2;
          border-radius: 7px;
          width: 98.4%;
          height: 97%;
          border: 1px solid $black4;
          overflow-y: scroll;
          overflow-x: hidden;

          @include scrollY($white1);
        }
        .blackOpsDivision {
          @include grid(25% 25% 25% 25%, "", center, center);
          grid-row-gap: 1vh;
          grid-column-gap: 0.05vw;
          background: $black2;
          border-radius: 7px;
          width: 98.4%;
          height: 97%;
          border: 1px solid $black4;
        }
      }

      @mixin border-department($color1, $color2) {
        border: 2px solid $color1;
        box-shadow: 0 0 10px $color2;
        background: linear-gradient(
          135deg,
          $black1 0%,
          $black2 25%,
          $black3 35%,
          $black4 50%,
          $black3 65%,
          $black2 75%,
          $black1 100%
        );
      }

      .border-staff {
        @include border-department($yellow3, $yellow2);
      }
      .border-aerospatialDepartment {
        @include border-department($yellow3, $yellow1);
      }
      .border-infantryDepartment {
        @include border-department($blueExplo, $blue2);
      }
      .border-logisticsDepartment {
        @include border-department($yellow3, $yellow1);
      }
      .border-militaryResearchDepartment {
        @include border-department($green3, $green2);
      }
      .border-militaryAcademy {
        @include border-department($orange3, $orange2);
      }
      .border-militaryMedicineDepartment {
        @include border-department($white1, $white1);
      }
      .border-blackOpsDivision {
        @include border-department($red4, $red4);
      }

      .fade-in {
        opacity: 1;
        transform: translate(-100%, -50%);
      }
      .fade-out {
        opacity: 0;
        transform: translate(0, -50%);
      }

      // militaryCareerCards.js ------------------------------------------------------------------------------
      .job-description-window {
        .job-title-staff {
          @include job-title($yellow3, $yellow1);
        }
        .job-title-aerospatialDepartment {
          @include job-title($yellow3, $yellow1);
        }
        .job-title-infantryDepartment {
          @include job-title($blueExplo, $blue2);
        }
        .job-title-logisticsDepartment {
          @include job-title($yellow3, $yellow1);
        }
        .job-title-militaryResearchDepartment {
          @include job-title($green3, $green1);
        }
        .job-title-militaryAcademy {
          @include job-title($orange3, $orange1);
        }
        .job-title-militaryMedicineDepartment {
          @include job-title($white1, $white1);
        }
        .job-title-blackOpsDivision {
          @include job-title($red4, $red4);
        }

        // Paragraphes de description des métiers
        .job-description {
          position: absolute;
          text-align: justify;
          width: 143%;
          top: 31vh;
          left: 50%;
          transform: translateX(-50%);
          border: 1px solid $black1;
          border-radius: 10px;
          line-height: 2vh;
          font-size: 1.1rem;
          letter-spacing: 2px;
          background: linear-gradient(
            135deg,
            $black1 0%,
            $black2 5%,
            $black3 50%,
            $black2 95%,
            $black1 100%
          );

          @include flex("", "", center, flex-start);

          .job-description-paragraphe {
            border: 1px solid $black2;
            border-radius: 7px;
            padding: 10px;
            margin: 5px;
            height: 22vh;
            overflow-y: scroll;
            background: $black3;

            &::-webkit-scrollbar {
              background: none;
              width: 0.2vw;
            }

            &::-webkit-scrollbar-thumb {
              border-radius: 10px;
              background: $grey2;
            }
          }
        }
      }

      .job-logo-container {
        border-radius: 50%;
        @include flex(column, "", center, center);
        text-align: justify;
      }

      .job-detail-container {
        position: absolute;
        z-index: 19;
        width: 46vw;
        height: 50vh;
        background: $black2;
        border-radius: 10px;
        background: linear-gradient(
          135deg,
          $black1 0%,
          $black2 25%,
          $black3 35%,
          $black4 50%,
          $black3 65%,
          $black2 75%,
          $black1 100%
        );

        .job-detail-background {
          position: absolute;
          border-radius: 7px;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border: 1px solid $black4;
          box-shadow: inset 0 0 10px $shadow;
          z-index: -1;
        }

        .come-back-btn-container {
          @include flex(row, "", top, center);
          position: fixed;
          width: 100%;
          height: 3vh;
          top: 0;
          left: 0;
          z-index: 25;
          font-size: 2rem;
          text-shadow: 0 0 5px $shadow;
          color: $grey2;
          padding: 1vh 2vw;
          border-radius: 5px;
          transition: 0.3s;

          &:hover {
            cursor: pointer;
            transform: translateX(-1vw);
            color: $yellow3;
            text-shadow: 0 0 10px $yellow1;
          }

          h4 {
            text-align: left;
            margin: 0 0.5vw;
            font-weight: 400;
            font-size: 1.2rem;
            letter-spacing: 2px;
          }
        }

        img {
          position: fixed;
          top: 40px;
          right: 40px;
        }

        h3 {
          margin: 7.5vh 0 6vh 0;
          padding: 1vh 10vw 1vh 2.1vw;
          text-align: left;
          text-transform: uppercase;
          font-size: 1.9rem;
          letter-spacing: 5px;
          background: $darkShadow;
        }

        .category {
          position: fixed;
          text-align: left;
          width: 42vw;
          margin: -6vh 0;
          padding: 0 2.1vw 1vh;
          font-size: 1rem;
          text-transform: uppercase;
          letter-spacing: 5px;
          background: $darkShadow;
          z-index: -1;
        }

        p {
          position: fixed;
          width: 42vw;
          margin: 2vh 0;
          padding: 1vh 2.1vw;
          font-size: 1.3rem;
          letter-spacing: 3px;
          text-align: justify;
          line-height: 2.3vh;
          background: $darkShadow;
        }

        .promotion {
          position: fixed;
          text-align: center;
          top: 88%;
          width: 100%;
          margin: 0;
          padding: 1.5vh 0;
          font-size: 1.2rem;
          text-transform: uppercase;
          letter-spacing: 5px;
          background: $darkShadow;
        }
      }

      .slide-in {
        top: 0;
        left: 0;
      }
      .slide-out {
        top: 0;
        left: -150vh;
      }

      // MIXIN
      @mixin deployed-military($height, $animationColor, $time) {
        height: $height;
        transition: 0.7s;
        animation: $animationColor $time infinite;
        border-radius: 50%;
      }
      // => Applciation immédiate de la mixin
      .deployed-military-staff {
        @include deployed-military(15vh, waveYellow, 2s);
      }
      .deployed-military-aerospatialDepartment {
        @include deployed-military(15vh, waveYellow, 2s);
      }
      .deployed-military-infantryDepartment {
        @include deployed-military(15vh, waveBlueExplo, 2s);
      }
      .deployed-military-logisticsDepartment {
        @include deployed-military(15vh, waveYellow, 2s);
      }
      .deployed-military-militaryResearchDepartment {
        @include deployed-military(15vh, waveGreen, 2s);
      }
      .deployed-military-militaryAcademy {
        @include deployed-military(15vh, waveOrange, 2s);
      }
      .deployed-military-militaryMedicineDepartment {
        @include deployed-military(15vh, waveWhite, 2s);
      }
      .deployed-military-blackOpsDivision {
        @include deployed-military(15vh, waveRedMed, 2s);
      }

      .reduced {
        height: 15vh;
        transition: 0.5s;
        border-radius: 50%;
        animation: waveRed 0.6s 0s forwards;
        @include flex(column, "", center, center);
        cursor: pointer;
      }

      // Fin des paragraphes

      .close-wdw-btn {
        position: absolute;
        @include flex(column, "", center, center);
        top: 15px;
        right: 5px;
        padding: 10px;
        transform: translate(10px, -10px) rotateZ(45deg);
        transition: 0.2s;

        &:hover {
          transform: translate(10px, -10px) rotateZ(135deg);
          cursor: pointer;

          span {
            background: $yellow1;
            animation: btnWaveRed 0.5s infinite;
          }
        }

        span {
          height: 2px;
          width: 20px;
          background: $white1;
          transition: 0.2s;
        }

        .first-bar {
          transform: translateY(2px) rotatez(90deg);
        }
      }

      // militaryCareerCards.js ENDING ------------------------------------------------------------------------

      .no-active-position {
        position: relative;
        top: auto;
        left: auto;
      }

      .active-position {
        position: absolute;
        top: 40px;
        left: 40px;
        z-index: 20;
      }

      .slide-out {
        position: absolute;
        transform: translateX(100px);
      }

      // militaryDepartments.js ENDING --------------------------------------------------------------------------
    }

    .career-plan {
      @include grid(100%, "", center, center);

      p {
        width: 95%;
        margin: 0 auto;
        color: $white2;
        font-size: 1.5rem;
        text-align: justify;
        letter-spacing: 2px;
        border: 1px solid $black3;
        padding: 10px;
        background: linear-gradient(
          135deg,
          $black1 0%,
          $black2 15%,
          $black3 50%,
          $black2 85%,
          $black1 100%
        );
        border-radius: 10px;
      }
    }
  }
}

// militaryCareers.js ENDING -----------------------------------------------------------------------------------
