.account-statment-container {
  width: 100%;
  max-width: 400px;
  margin-bottom: 50vh;
  @include flex(column, "", center, flex-start);

  .title {
    text-align: left;
    padding-left: 10px;
    font-size: 1.5rem;
    letter-spacing: 4px;
    text-transform: uppercase;
  }

  .account-profil-container {
    position: relative;
    width: 100%;
    padding-left: 10px;
    @include flex("row", wrap, "flex-start", "top");

    .pictures-container {
      height: 110px;
      width: 80px;
      border-radius: 5px;

      .profil-picture-container {
        border: 1px solid $white2;
        width: 78px;
        height: 80px;
        border-radius: 4px 4px 0 0;

        img {
          width: 78px;
          height: 80px;
        }
      }

      .rank-container {
        @include flex("row", "wrap", "", "");
        .corpo-picture-container {
          @include flex("row", "wrap", center, center);
          border: 1px solid $white2;
          width: 40px;
          height: 30px;
          border-top: none;
          border-right: none;
          border-radius: 0 0 0 4px;

          img {
            width: 24px;
            height: auto;
          }
        }
        .rank-picture-container {
          @include flex("row", "wrap", center, center);
          border: 1px solid $white2;
          width: 40px;
          height: 30px;
          border-top: none;
          border-radius: 0 0 4px 0;

          img {
            width: 24px;
            height: auto;
          }
        }
      }
    }

    .account-infos-container {
      padding-left: 10px;

      .current-user-pseudo {
        margin: 0;
        font-size: 1.5rem;
        letter-spacing: 2px;
      }

      .edit-display-name {
        position: absolute;
        top: 6px;
        right: 20px;
      }

      .role-site-container {
        @include flex("row", "wrap", center, center);
        width: fit-content;
        margin: 5px 2px 0;

        .role-circle {
          height: 10px;
          width: 10px;
          border-radius: 50%;
        }

        .administrateur {
          background: $red5;
          border: 1px solid $red3;
          box-shadow: 0 0 10px 3px $red4;
        }

        .membre {
          background: $blue4;
          border: 1px solid $blue3;
          box-shadow: 0 0 10px 3px $blueExplo;
        }

        .invité {
          background: $grey1;
          border: 1px solid $grey4;
          box-shadow: 0 0 10px 3px $white1;
        }

        .user-role {
          margin: 5px 10px;
          letter-spacing: 2px;
          font-size: 1.1rem;
          text-transform: capitalize;
        }
      }

      .role-corpo-container {
        @include flex("row", "wrap", center, center);
        width: fit-content;
        margin: 0 2px;

        .role-circle {
          height: 10px;
          width: 10px;
          border-radius: 50%;
        }

        .primarque {
          background: $yellow2;
          border: 1px solid $yellow1;
          box-shadow: 0 0 10px 3px $yellow2;
        }

        .executeur {
          background: $orange2;
          border: 1px solid $orange1;
          box-shadow: 0 0 10px 3px $orange2;
        }

        .membre {
          background: $blue4;
          border: 1px solid $blue3;
          box-shadow: 0 0 10px 3px $blueExplo;
        }

        .invité {
          background: $grey1;
          border: 1px solid $grey4;
          box-shadow: 0 0 10px 3px $white1;
        }

        .user-role {
          margin: 5px 10px;
          letter-spacing: 2px;
          font-size: 1.1rem;
          text-transform: capitalize;
        }
      }
    }
  }
  .logout-btn {
    @include flex(column, "", center, center);
    margin-top: 50px;
    width: 100%;
    font-size: 1.2rem;
    transform: translate(0, 25px);

    button {
      position: relative;
      width: 200px;
      height: 50px;
      border-radius: 5px;
      font-size: 1.2rem;
      text-transform: uppercase;
      letter-spacing: 4px;
      background: $black4;
      color: $white1;
      font-family: $agFBB;
      border: none;
      outline: none;
      fill: $black3;
      transition: 0.2s;
      box-shadow: 0 0 10px $shadow;

      &:hover {
        cursor: pointer;
        background: $yellow1;
        color: $black2;
      }

      // .clipped-corner {
      //   position: absolute;
      //   width: 26px;
      //   height: 26px;
      // }

      // .top-left {
      //   top: 0;
      //   left: 0;
      // }
    }

    span {
      margin: 20px auto;
      font-size: 1.2rem;
      letter-spacing: 1px;
      color: $red4;
    }
  }
}
