.new-post-modal {
  margin: 5vh 0 2vh 0;
  background: linear-gradient(
    135deg,
    $black1 0%,
    $black2 5%,
    $black3 50%,
    $black2 95%,
    $black1 100%
  );
  padding: 0.5vh 1vw;
  border-radius: 10px;
  box-shadow: 0 0 10px $shadow;
  border: 1px solid $black1;

  .new-post-header {
    @include flex(row, "", space-between, center);

    .user {
      letter-spacing: 2px;
    }
  }
  form {
    position: relative;
    padding: 0 0 30px 0;

    textarea {
      resize: none;
      border: none;
      border-radius: 0px 10px 0 10px;
      padding: 1%;
      width: 98%;
      height: 10vh;
      transition: 0.2s;
      font-size: 1.3rem;
      background: $white1;
      border-top: 1px solid $red3;
      border-left: 1px solid $red3;
      letter-spacing: 1px;

      &:focus {
        border: none;
        outline: none;
        box-shadow: inset 0 0 10px $grey4;
        border-top: 1px solid $yellow1;
        border-left: 1px solid $yellow1;
        background: linear-gradient(
          135deg,
          $black1 0%,
          $black2 5%,
          $black3 50%,
          $black2 95%,
          $black1 100%
        );
        color: $white2;
        letter-spacing: 1px;
      }
    }

    input {
      position: absolute;
      font-size: 1.1rem;
      font-weight: $agFBB;
      bottom: 3px;
      right: -1px;
      padding: 3px 10px 0 10px;
      border-radius: 5px 0px 5px 5px;
      border: 1px solid $translucent;
      text-transform: uppercase;
      letter-spacing: 3px;
      text-align: center;
      transition: 0.2s;

      &:hover {
        border: 1px solid $yellow1;
        background: linear-gradient(
          135deg,
          $black1 0%,
          $black2 5%,
          $black3 50%,
          $black2 95%,
          $black1 100%
        );
        color: $white1;
        box-shadow: inset 0 0 5px $grey4;
      }
    }
  }
}
