.comment-container {
  width: 100%;

  .comment-title {
    margin: 10px 0 0 0;
    padding: 5px 0px;
    letter-spacing: 2px;
    font-size: 1.2rem;
    color: $grey3;
  }

  .comment-redaction-container {
    textarea {
      border: none;
      border-bottom: 1px solid $translucent;
      border-left: 1px solid $translucent;
      padding: 5px 1% 0 1%;
      width: 97.8%;
      margin: 0 auto;
      border-radius: 5px 5px 5px 0px;

      &:focus {
        border-bottom: 1px solid $red3;
        border-left: 1px solid $red3;
      }
    }

    input {
      font-size: 1.1rem;
      font-weight: $agFBB;
      padding: 3px 10px 0 10px;
      border-radius: 0px 5px 5px 5px;
      border: 1px solid $translucent;
      text-transform: uppercase;
      letter-spacing: 3px;
      text-align: center;
      transition: 0.2s;

      &:hover {
        border: 1px solid $yellow1;
        background: linear-gradient(
          135deg,
          $black1 0%,
          $black2 5%,
          $black3 50%,
          $black2 95%,
          $black1 100%
        );
        color: $white1;
        box-shadow: inset 0 0 5px $grey4;
      }
    }
  }

  .comment-redaction-container {
    p {
      margin: 30px 10px 20px;
      color: $orange2;
    }
  }
}
