.contact-body {
  margin: 70px auto 0;
  background: $black3;
  position: relative;
  overflow-y: hidden;

  .ember-effect {
    top: 0;
    height: 100vh;
  }

  .contact-title-container {
    @include flex(column, "", center, center);
    top: 0;
    height: 70px;
    width: 100%;
    position: absolute;
    background: radial-gradient(circle, #330000 3%, #000000 100%);
    box-shadow: 0 0 10px $black1;

    h2 {
      font-size: 2rem;
      padding: 5px;
      background: -webkit-linear-gradient(95deg, $yellow1, $white1, $yellow1);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      -webkit-filter: drop-shadow(0 0 5px $yellow1);
      letter-spacing: 10px;
      animation: titleWaveYellow 3s infinite;
      text-transform: uppercase;
    }
  }

  .contact-form-general-container {
    @include flex(column, "", center, center);
    width: 100%;
  }
}
