.connect-modal-container {
  @include flex(column, "", center, center);
  position: fixed;
  top: 70px;
  right: 0;
  background: $black2;
  height: 94.8vh;
  width: 20vw;
  max-width: 400px;
  border-radius: 5px;
  transition: 0.4s;

  .go-back-btn {
    position: relative;
    padding-left: 10px;
    @include flex(row, "", flex-start, center);
    width: 100%;
    height: 50px;
    background: $black2;
    border: none;
    color: $white2;
    font-size: 1.3rem;
    letter-spacing: 2px;
    text-align: center;

    &:hover {
      cursor: pointer;
      background: $black4;
      color: $white1;

      i,
      span {
        transform: translateX(20px);
      }
    }

    span {
      position: absolute;
      left: 30px;
      top: 12px;
    }

    i,
    span {
      transition: 0.2s;
    }
  }

  .enlist-root {
    @include flex(column, "", center, center);
    height: 100%;
    width: 100%;

    .submit-btn {
      @include flex(column, "", center, flex-end);
      width: 100%;
      font-size: 1.2rem;

      button {
        position: relative;
        width: 120px;
        height: 50px;
        border-radius: 5px;
        font-size: 1.5rem;
        text-transform: uppercase;
        letter-spacing: 2px;
        background: $black4;
        color: $white1;
        font-family: $agFBB;
        border: none;
        outline: none;
        fill: $black3;
        transition: 0.2s;
        box-shadow: 0 0 10px $shadow;

        &:hover {
          cursor: pointer;
          background: $yellow1;
          color: $black2;
        }

        // .clipped-corner {
        //   position: absolute;
        //   width: 26px;
        //   height: 26px;
        // }

        // .top-left {
        //   top: 0;
        //   left: 0;
        // }
      }
    }

    .sign-in {
      transform: translate(-111px, -135px);
      position: relative;
      padding: 12.5px 22px;
      border-radius: 5px;
      font-size: 1.3rem;
      text-transform: uppercase;
      letter-spacing: 2px;
      background: $black4;
      color: $white1;
      font-family: $agFBB;
      border: none;
      outline: none;
      fill: $black3;
      box-shadow: 0 0 10px $shadow;

      &:hover {
        cursor: pointer;
        background: $yellow1;
        color: $black2;
      }
    }

    .login {
      transform: translate(-95px, -30px);
      position: relative;
      padding: 12.5px 22px;
      border-radius: 5px;
      font-size: 1.3rem;
      text-transform: uppercase;
      letter-spacing: 2px;
      background: $black4;
      color: $white1;
      font-family: $agFBB;
      border: none;
      outline: none;
      fill: $black3;
      box-shadow: 0 0 10px $shadow;

      &:hover {
        cursor: pointer;
        background: $yellow1;
        color: $black2;
      }
    }
  }
}

.deployed {
  transform: translateX(0);
}

.retracted {
  transform: translateX(100%);
}
